import React from "react"
import { graphql } from "gatsby"
import { ThemeProvider } from "styled-components"
import Layout from "../layouts/home/home"
import theme from "../constants/theme"
import { H2, PostCard } from "../components"
const Categorypage = ({ data }: any) => (
  <ThemeProvider theme={theme}>
    <Layout>
      <H2
        color="default"
        style={{
          textTransform: "capitalize",
          margin: "4rem 0rem",
          textAlign: "center",
        }}
      >
        Posts About {data.allMdx.edges[0].node.frontmatter.category}
      </H2>
      {data.allMdx.edges.map((post: any) => (
        <PostCard
          key={post.node.id}
          title={post.node.frontmatter.title}
          excerpt={post.node.frontmatter.des}
          url={post.node.frontmatter.path}
          img={post.node.frontmatter.thumbnail}
          tags={post.node.frontmatter.tags}
        ></PostCard>
      ))}
    </Layout>
  </ThemeProvider>
)
export const data = graphql`
  query CategoryIndex($category: String) {
    allMdx(filter: { frontmatter: { category: { eq: $category } } }) {
      edges {
        node {
          id
          excerpt
          frontmatter {
            author
            thumbnail
            des
            date
            path
            title
            category
            tags {
              tag
              color
            }
          }
        }
      }
    }
  }
`

export default Categorypage
